export default "var elm = document.getElementById(\"animationTest\");\nvar box = tofu.domAppend(\"div\", elm);\n\ntofu.setStyles(box, {\n  display: \"block\",\n  position: \"relative\",\n  width: \"200px\",\n  height: \"200px\",\n  background: \"red\",\n  cursor: \"pointer\",\n});\n\ntofu.click(box, function () {\n  tofu.animate(this, \"left\", 500, 1000, function () {\n    tofu.animate(this, {\n      width: 50,\n      height: 50,\n      fontSize: 25,\n      opacity: .5\n    }, 500);\n  });\n});"